.container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
    background-color: #2f303a;
    box-shadow: 0px 0px 8px rgba(0,0,0,.6);
    color: #fff;
};

.contacts_container {
    display: flex;
    flex-direction: column;
    padding: 30px;
}

.address {
    margin-top: 8px;
    font-style: normal;
}

.contacts_item {
    margin-top: 12px;
    margin-bottom: 0px;
};

.contacts_link {
    text-decoration: none;
    color: #fff;

    &:hover {
        color: orange;
    }
};


.messenger {
    width: 50px;
    height: 50px;
    color: #fff;
}